import { Link } from "react-router-dom";
import Footer from "../components/Footer";
import MenuBar from "../components/MenuBar";
import "../css/App.css";

function Prices() {
  return (
    <div className="page">
      <MenuBar transparent={false} />
      <div
        className="container"
        style={{
          height: "auto",
          marginBottom: 64
        }}
      >
        <h2 style={{ marginTop: 24 }}>HINNASTO</h2>
        <p>
          Alla oleva hinnasto ksitt kaikki portfoliostani lytyvt
          kuvausvaihtoehdot. Mikli toivomaasi kuvausta ei lydy tarjonnastani,
          kysythn siit erikseen minulta. Tapahtumat ja tilaisuudet
          hinnoitellaan aina yksilllisesti asiakkaan tarpeiden mukaan. Kuvausta
          varatessa laskutan sinulta kuvausmaksun osuuden, joka toimii
          varausvahvistuksena ja kuvausehtojeni hyvksymisen. Luethan
          kuvausehtosopimukseni{" "}
          <Link className="link" to={"/terms"}>
            tlt
          </Link>{" "}
          ennen kuvauksen varaamista. Kuvausmaksu sislt kuvauksen
          suunnittelun, tunnin kuvausajan (newborn-kuvaus 2 tuntia) sek
          esivalintagallerian. Kuvapaketti ostetaan erikseen kuvauksen jlkeen.
        </p>
        <div
          style={{
            width: "auto",
            maxWidth: 380,
            height: "auto",
            margin: "auto",
            border: "2px solid rgba(0, 0, 0, 0.05)",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            marginTop: 24,
            background: "#FAF7F6"
          }}
        >
          <div
            style={{
              fontSize: 18,
              padding: 32
            }}
          >
            <p
              style={{
                textAlign: "center",
                fontWeight: "bold"
              }}
            >
              ENNEN KUVAUSTA:
            </p>
            <p
              style={{
                textAlign: "center"
              }}
            >
              KUVAUSMAKSU 100
            </p>
            <p
              style={{
                textAlign: "center",
                fontWeight: "bold"
              }}
            >
              KUVAUKSEN JLKEEN:
            </p>
            <p
              style={{
                textAlign: "center"
              }}
            >
              10 KUVAA 50
            </p>
            <p
              style={{
                textAlign: "center"
              }}
            >
              20 KUVAA 80
            </p>
            <p
              style={{
                textAlign: "center"
              }}
            >
              KOKO GALLERIA 120
            </p>
            <p
              style={{
                textAlign: "center",
                fontSize: 14
              }}
            >
              (Kaikki hinnat sisltvt alv. 24%.)
            </p>
            <Link to={"/contact"} style={{ textDecoration: "none" }}>
              <input type="submit" value="VARAA NYT" className="formInput" />
            </Link>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
}

export default Prices;
